$primaryColor: #4527a4;
$secondaryColor: #8344c5;

.csbInspectorInfo {
	padding: 6px;

	a {
		text-decoration: none;
		font-weight: bold;
	}
}

// For Pattern
.block-editor-inserter__panel-content {
	.block-editor-block-preview__content {
		min-height: 176px;

		>iframe {
			min-height: 706px;

			.block-editor-block-preview__content-iframe {
				height: auto;
			}
		}
	}
}

// Content Slider Block
.wp-block-csb-content-slider-block {
	.csbContentSlider {
		.swiper-slide {

			.slideTitle,
			.slideDesc,
			.slideBtn {
				cursor: move;
			}
		}
	}
}